import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ApiService from "./common/api.service";

Vue.config.productionTip = false

// Importing the global css file
import "@/styles.css"

import './registerServiceWorker'
ApiService.init();

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')