import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Splash',
    component: () => import(/* webpackChunkName: "about" */ '../views/splash.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "about" */ '../views/login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "about" */ '../views/register.vue')
  },
  {
    path: '/location',
    name: 'Location',
    component: () => import(/* webpackChunkName: "about" */ '../views/location.vue')
  },
  {
    path: '/forgot-password',
    name: 'Forgot-password',
    component: () => import(/* webpackChunkName: "about" */ '../views/forgot-password.vue')
  },
  {
    path: '/new-password',
    name: 'New-password',
    component: () => import(/* webpackChunkName: "about" */ '../views/new-password.vue')
  },
  {
    path: '/select-sport',
    name: 'Select-sport',
    component: () => import(/* webpackChunkName: "about" */ '../views/select-sport.vue')
  },
  {
    path: '/set-tennis-profile',
    name: 'Set-tennis-profile',
    component: () => import(/* webpackChunkName: "about" */ '../views/configure-profile.vue')
  },
  {
    path: '/set-tennis-federation',
    name: 'Tennis-Federation',
    component: () => import(/* webpackChunkName: "about" */ '../views/configure-profile-2.vue')
  },
  {
    path: '/set-tennis-equipment',
    name: 'Tennis-quipment',
    component: () => import(/* webpackChunkName: "about" */ '../views/configure-profile-3.vue')
  },
  {
    path: '/my-profile',
    name: 'My-profile',
    component: () => import(/* webpackChunkName: "about" */ '../views/my-profile.vue')
  },
  {
    path: '/home',
    name: 'Home',
    component: () => import(/* webpackChunkName: "about" */ '../views/home.vue')
  },
  {
    path: '/players',
    name: 'Players',
    component: () => import(/* webpackChunkName: "about" */ '../views/players.vue')
  },
  {
    path: '/challenge',
    name: 'Challenge',
    component: () => import(/* webpackChunkName: "about" */ '../views/challenge.vue')
  },
  {
    path: '/preferences',
    name: 'Preferences',
    component: () => import(/* webpackChunkName: "about" */ '../views/preferences.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
